import { useState, useEffect } from "react";
import Link from "next/link";
import Router from "next/router";
import { isAuth, getCookie } from "../../actions/auth";
import { create, getCategories, removeCategory, singleCategory } from "../../actions/category";

const Category = () => {
    const [values, setValues] = useState({
        name: "",
        error: false,
        success: false,
        categories: [],
        removed: false,
        reload: false
    });

    const { name, error, success, categories, removed, reload } = values;
    const token = getCookie("token");

    useEffect(() => {
        loadCategories();
    }, [reload]);
    const loadCategories = () => {
        getCategories().then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                setValues({ ...values, categories: data });
            }
        });
    };

    const showCategories = () => {
        return categories.map((el, index) => {
            return (
                <button
                    onDoubleClick={() => deleteConfirm(el.slug)}
                    title="Double click to delete"
                    key={index}
                    className="btn btn-outline-primary mr-1 ml-1 mt-3 "
                >
                    {el.name}
                </button>
            );
        });
    };

    const deleteConfirm = slug => {
        let answer = window.confirm("Are you sure you want to delete this category? ");
        if (answer) {
            deleteCategory(slug);
        }
    };

    const deleteCategory = slug => {
        console.log("delete", slug);

        removeCategory(slug, token).then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                setValues({ ...values, error: false, success: false, name: "", removed: !removed, reload: !reload });
            }
        });
    };

    const clickSubmit = e => {
        e.preventDefault();
        create({ name }, token).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error, success: false });
            } else {
                setValues({ ...values, error: false, success: true, name: "", removed: false, reload: !reload });
            }
        });
    };

    const handleChange = e => {
        setValues({
            ...values,
            name: e.target.value,
            error: false,
            success: false,
            removed: ""
        });
    };

    const showSuccess = () => {
        if (success) {
            return <p className="position-absolute text-success">Category is created</p>;
        }
    };

    const showError = () => {
        if (error) {
            return <p className="position-absolute text-danger">Category already exist</p>;
        }
    };

    const showRemoved = () => {
        if (removed) {
            return <p className="position-absolute text-danger">Category is removed</p>;
        }
    };

    const newCategoryForm = () => (
        <form onSubmit={clickSubmit}>
            <div className="form-group">
                <label className="mt-4 text-muted">Category Name</label>
                <input type="text" className="form-control" onChange={handleChange} value={name} required />
            </div>
            <button type="submit" className="btn btn-primary">
                Create
            </button>
        </form>
    );

    const mouseMoveHandler = () => {
        setValues({ ...values, error: false, success: false, removed: "" });
    };

    return (
        <React.Fragment>
            {showSuccess()}
            {showError()}
            {showRemoved()}

            <div onMouseMove={mouseMoveHandler}>
                {newCategoryForm()}
                {showCategories()}
            </div>
        </React.Fragment>
    );
};

export default Category;
